.margin-bottom-0 {
  margin-bottom: 0;
}

.margin-top-30 {
  margin-top: 30px;
}

.margin-right-10 {
  margin-right: 10px;
}

.margin-left-10 {
  margin-left: 10px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.list-thumb {
    width: 260px;
    display: inline-block;
    border: 1px solid black;
    padding: 10px;
    margin-right: 20px;
    margin-bottom: 14px;
    font-size: .9em;
}

.list-thumb > h2 {
    font-size: 1.2em;
}
#progress input {
    width: 65px;
    height: 50px;;
    text-align: center;
}

#progress .input-control {
    width: 65px;
    height: 50px;
}

#progress .submit {
    margin-left: 10px;
    padding-left: 15px;
    padding-right: 15px;
    height: 50px;
}



#progress span {
    display: block;
    font-size: .75em;
    padding-left: 50px;
}
